@import "core/reset";

/*Variables*/
$robotoslab-font: "RobotoSlab-Bold";
$white-color: white;
$orange-color: #f26002;
$capitalize-text: capitalize;
$background-center: center center;
$size-20px: 20px;
$size-24px: 24px;
$size-40px: 40px;

/*HEADER*/
.image_container {
  background-image: url("../images/cover.jpeg");
  max-width: 100vw;
  height: 100vh;
  background-size: cover;
  background-position: $background-center;
  display: flex;
}

.header_menu_container {
  background-image: url("../images/ico-menu.svg");
  background-color: #0271bc;
  background-repeat: no-repeat;
  background-position: $background-center;
  height: 22px;
  width: 37px;
  position: fixed;
}

.header_text_container {
  color: $white-color;
  display: flex;
  text-align: center;
  justify-content: flex-end;
  gap: 15px;
  flex-direction: column;
  padding-bottom: 25px;
}

.h1_text {
  font-size: 42px;
  font-family: $robotoslab-font;
  text-transform: uppercase;
}

.button_circle {
  display: flex;
  background-image: url("../images/ico-scroll-down.svg");
  border-radius: 50%;
  background-position: $background-center;
  background-repeat: no-repeat;
  background-color: $white-color;
  width: $size-40px;
  height: $size-40px;
  position: relative;
  margin: 0 auto;
}

/*MAIN*/
/*First-section*/

.first_section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 376px;
  margin-left: 15px;
  margin-right: 15px;
  gap: 15px;
  line-height: 30px;
}

.first_text {
  color: #8ccbea;
  font-size: 14px;
  text-transform: $capitalize-text;
}

.second_text {
  font-size: $size-24px;
  font-family: $robotoslab-font;
  font-weight: bold;
  text-transform: $capitalize-text;
}

.third_text {
  color: #4a4a4a;
  font-size: 18px;
}

.go_button {
  color: #f26002;
  font-size: $size-20px;
  font-weight: bold;
  text-transform: $capitalize-text;
  border: 2.5px solid $orange-color;
  width: 76px;
  height: 45px;
  border-radius: $size-40px;
  padding-top: 4px;
  margin-top: 15px;
  text-align: center;
  cursor: pointer;
}
.go_button:hover {
  background-color: #304268;
  opacity: 0.7;
}

/*second-section*/

.second_section {
  background-color: #eff9ff;
  height: 624px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 90px 70px 70px 70px 70px;
  row-gap: 45px;
  text-align: center;
  padding-left: 68px;
  padding-right: 68px;
}

.h2_title {
  font-size: $size-24px;
  font-family: $robotoslab-font;
  text-transform: $capitalize-text;
  padding-top: 29px;
}

.first_paragraph {
  font-size: 18px;
  color: black;
  font-family: $robotoslab-font;
  font-weight: bold;
}
.second_paragraph {
  font-size: 18px;
}
.reasons_button {
  background-color: $orange-color;
  color: $white-color;
  font-size: $size-20px;
  align-items: center;
  height: 45px;
  width: 184px;
  border-radius: 22.5px;
  padding: 10px 25px 10px 25px;
  cursor: pointer;
  transition: background-color 1s linear;
}
.reasons_button:hover {
  background-color: #0271bc;
  margin-top: 50px;
  opacity: 0.8;
}
/*footer*/

.footer {
  background-color: #304268;
  color: $white-color;
  text-transform: uppercase;
  height: 465px;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.arrow_button {
  display: flex;
  background-image: url("../images/ico-arrow.svg");
  background-position: $background-center;
  background-repeat: no-repeat;
  background-color: $white-color;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  transform: translateY(-25px);
  position: relative;
  margin: 0 auto;
  animation-name: change-color;
  animation-duration: 2s;
  cursor: pointer;
}
.list_footer {
  display: flex;
  list-style: none;
  flex-direction: column;
  text-align: center;
  gap: 10px;
  padding-top: $size-40px;
}
.link {
  text-decoration: none;
  color: $white-color;
}
.copy_footer {
  padding-top: 32px;
  text-transform: lowercase;
}
.heart {
  color: $orange-color;
}

/*MEDIA-QUERIES:Tablet*/

/*Header*/
@media (min-width: 768px) {
  .header_menu_container {
    height: 44px;
    width: 41.6px;
  }
  .header_text_container {
    width: 100%;
  }
  .h1_text {
    font-size: 80px;
  }
  .hero_text {
    font-size: $size-24px;
    margin-right: 250px;
    margin-left: 250px;
  }
  .button_circle {
    height: 45px;
    width: 42.5px;
  }
  /*MAIN*/
  /*first-section*/

  .second_text {
    font-size: 26px;
  }
  .third-text {
    font-size: $size-20px;
  }
  .go_button {
    width: 91px;
    height: 45px;
  }

  /*second-section*/
  .second_section {
    background-color: #eff9ff;
    height: 397px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 100px 100px 100px;
    text-align: center;
    row-gap: 38px;
  }
  .h2_title {
    grid-column: 1/4;
    font-size: 26px;
  }
  .div_button {
    grid-column: 2/3;
  }
  .first_paragraph {
    font-size: $size-24px;
  }
  .second_paragraph {
    font-size: $size-20px;
  }
  .reasons_button {
    height: 45px;
    width: 210px;
    font-size: $size-20px;
  }

  /*FOOTER*/

  .footer {
    height: 240px;
  }
  .footer-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 39px;
    padding-right: 39px;
  }
  .copy_footer {
    order: -1;
  }
}

/*Desktop*/
/*HEADER*/

@media (min-width: 1200px) {
  .h1_text {
    font-size: 100px;
    padding-left: 275px;
    padding-right: 275px;
  }
  .button_circle {
    height: 45px;
    width: 45px;
  }

  /*MAIN*/
  /*First-section*/
  .first_section {
    padding-left: 275px;
    padding-right: 275px;
  }
  .first_text {
    font-size: 16px;
  }
  .second_text {
    font-size: 32px;
  }
  .third_text {
    font-size: $size-20px;
  }
  .go_button {
    width: 160px;
  }

  /*second-section*/
  .second_section {
    padding-left: 81px;
    padding-right: 81px;
  }
  .h2_title {
    font-size: 32px;
  }
  .first_paragraph {
    font-size: $size-24px;
  }
  .second_paragraph {
    font-size: $size-20px;
  }
  .reasons_button {
    width: 255px;
    font-size: $size-20px;
  }

  /*FOOTER*/
  .footer {
    padding-left: 170px;
    padding-right: 170px;
  }
}

@keyframes change-color {
  0% {
    transform: scaleY(1);
  }
  30% {
    transform: scaleY(2);
    background-color: red;
    opacity: 0.5;
  }
}
